// mobile buttons

.button-header {
  font-family: Montserrat;
  text-align: center;
  background: var(--acc-color);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: normal;

  @include font(14px, 600, 17px);
  @include padding-button(12px, 12px, 33px, 33px);

  &:hover,
  &:focus {
    background-color: #0a8817;
  }
}

.button-header__svg {
  margin-left: 16px;
  fill: #000000;
}

.button-hero {
  font-family: Montserrat;
  text-align: center;
  border: 2px solid #f8d24e;
  border-radius: 8px;
  @include font(14px, 600, 17px);
  @include padding-button(12px, 12px, 36px, 36px);

  &:hover,
  &:focus {
    border-color: #0a8817;
  }
}
