@media screen and (min-width: 320px) {

    html,
    body {
        overflow-x: hiden;
    }
}

body{
    font-family: "Montserrat", sans-serif;
    font-weight: 500;

}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    margin: 0;
}

body {
    
    background-color: var(--modal-bcg);
}


ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

link {
    text-decoration: none;
    
}

button {
    line-height: 0px;
    border: none;
    background: none;
    padding: 0px;
}